.wrapper-modal {position: fixed; inset: 0; display: flex; justify-content: center; align-items: center; background: rgba(0,0,0,0.71);}
.presentation {position: absolute; z-index: 1200;}
.modal-close {position: absolute; right: 20px; top: 20px; cursor: pointer; z-index: 1000; color: #fff;}
.modal {position: relative; max-width: 800px; box-shadow: 0 3px 5px -1px rgba(0,0,0,0.2), 0 5px 8px 0 rgba(0,0,0,0.14), 0 1px 14px 0 rgba(0,0,0,0.12); background: #111; overflow: hidden; border-radius: 8px; transition: .35s; animation: fadeIn .35s;}
.modal__poster-img {width: 100%; height: auto;}
.modal__contents {padding: 40px; color: #fff;}
.modal__title {padding: 0; margin: 16px 0; font-size: 40px;}
.modal__details {font-weight: 600; font-size: 18px;}
.modal__overview {font-size: 20px; line-height: 1.5;}
.modal::-webkit-scrollbar {display: none; visibility: hidden;}

@keyframes fadeIn {
  from {opacity: 0; transform: scale(0.5);}
  to {opacity: 1; transform: scale(1);}
}

@media screen and (max-height: 768px) {
  .wrapper-modal {align-items: unset; padding-top: 2rem;}
  .modal {overflow-y: scroll;}
}

@media screen and (max-width: 768px) {
  .wrapper-modal {padding: 0;}
  .modal {overflow-y: scroll !important;}
  .modal__overview {font-size: 16px;}
  .modal__details {font-size: 16px;}
}